.services_container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 100px;
}

.services{
    display: flex;
    flex-direction: column;
    margin-top: 110px;
    background: var(--color-bg-variant);
    border-radius: 7px 7px 2rem 2rem;
    border: 1px solid transparent;
    height: fit-content;
    transition: var(--transition);
}

.services:hover{
    background: transparent;
    border-color:var(--color-primary);
    cursor: default;
}

h3{
    background: var(--color-primary);
    color: var(--color-bg);
    border-radius: 7px 7px 0 0;
    padding: 15px 30px;
    width: 100%;
    font-size: 30px;
}

.service_list{
    display: flex;
    flex-direction: column;
    margin: 2rem 5rem;
}

.service_list li{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 40px;
}


@media screen and (max-width: 1024px) {
    #services{
        margin-top: -100px;
    }

    .services{
        margin: 0;
    }

    .services_container{
        flex-wrap: wrap;
        gap: 40px;
    }

}

/*========================== SMALL DEVICES =========================*/

@media screen and (max-width: 600px) {
    #services{
        margin-top: 200px;
    }

    .services_container{
        flex-direction: column;
        justify-content: center;
        gap: 1px;
    }

    .services{
        margin-top: 30px;
    }

    .service_list{
        margin: 20px 40px;
    }
}