.about_cards{
    display: grid;
    grid-area: b;
    grid-template-columns: 160px;
    gap: 1.5rem;
    justify-content: center;

}

.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
    transition: var(--transition);
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-top: 1rem;
}

.about_card h5{
    font-size: 1.4rem;
    font-weight: 600;
}

.about_card small{
    font-size: 1.1rem;
    color: var(--color-light);
}

/*========================== MEDIUM DEVICES =========================*/

@media screen and (max-width: 1024px) {

}

/*========================== SMALL DEVICES =========================*/

@media screen and (max-width: 600px) {
    .about_cards{
        display: flex;
        flex-direction: row;
        gap: 10px;
        margin-top: 60px;
    }

    .about_card{
        padding: 2px 6px;
    }

    .about_card h5{
        font-size: 1rem;
        font-weight: 600;
    }

    .about_card small{
        font-size: 0.9rem;
        color: var(--color-light);
    }
}