header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*========================== MEDIUM DEVICES =========================*/

@media screen and (max-width: 1024px) {

}

/*========================== SMALL DEVICES =========================*/

@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }

    .scroll__down, .header__socials:after, .header__socials a{
        display: none;
    }
}