.about_me{
    grid-area: a;
    width: 100%;
    height: 100%;
    aspect-ratio: 3/4;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: flex;
    place-items: center;
}

.about_me_img{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: 0.3s;
}

.about_me_img img{
    height: 600px;
}

.about_me_img:hover{
    transform: rotate(0deg);
    transition: 0.3s;
}

/*========================== MEDIUM DEVICES =========================*/

@media screen and (max-width: 1024px) {
    .about_me_img img{
        height: 500px;
        width: 300px;
    }

    .about_me{
        height: 500px;
        width: 300px;
    }
}

/*========================== SMALL DEVICES =========================*/

@media screen and (max-width: 600px) {
    .about_me_img img{
        height: 300px;
        width: 300px;
    }

    .about_me{
        height: 300px;
        width: 300px;
    }
}