.scroll__down{
    font-size: 1.6rem;
    margin: 35px 5px;
}

.box{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    display: flex;
    flex-direction: column;
    transform: rotate(90deg);
    transition: var(--transition);
}

.box:hover{
    transform: rotate(0deg);
}